import styled from 'styled-components'

export default styled.div`
  background: white;
  border-radius: 4px;
  border: 1px solid rgba(0,0,0,.15);
  box-shadow: 0 6px 12px rgba(0,0,0,.175);
  margin: 2px 0 0;
  padding: 5px 0;
  position: absolute;
  right: 0;
  top: 100%;
  user-select: none;
  z-index: 2;
`



