import styled from 'styled-components'

export default styled.section`
  cursor: pointer;
  padding: 10px 0;
  padding-bottom: 0;
  margin: 0;

  &:hover {
    color: #0466bd;
  }

  @media screen and (min-width: 40em) {
    margin: 10px 0;
  }
`



